import { Link } from 'react-router-dom';

const MyOrders = (props) => {
    const cancelOrder = (event) => {
        event.preventDefault();
        const [id, status] = event.target.value.split(',');
        console.log(id, status);
        props.cancelOrder(id, status);
    }

    const renderOrderBtn = (status, id) => {
        if(status === "Product Delivered") {
            return (
                <th>
                    <button className="btn btn-danger" onClick={cancelOrder} value={[id, 'Deleted']}>Delete</button>
                </th>
            );
        }
        else {
            return (
                <th>
                    <button className="btn btn-danger" onClick={cancelOrder} value={[id, 'Cancelled']}>Cancel</button>
                </th>
            );
        }
    }
    const displayRender = ({orders}) => {
        console.log(orders, props)
        if(orders && orders.length > 0) {
           return orders.map((order)=>{
                return (
                    <div key={order.id}>
                        <div className="panel panel-default">
                            <div className="panel-heading">
                                <table className="table table-responsive">
                                    <thead style={{backgroundColor:"#347B98"}}>
                                        <tr>
                                            <th>ORDER DATE</th>
                                            <th>DELIVEREY TO</th>
                                            <th>CONTACT NO</th>
                                            <th>PAYMENT MODE</th>
                                            <th>ORDER STATUS</th>
                                            <th>CANCEL / DELETE</th>
                                            <th></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr>
                                            <td>{order.date}</td>
                                            <td>{order.fname} {order.lname}</td>
                                            <td>{order.phone}</td>
                                            <td>{order.payment}</td>
                                            <td>{order.status}</td>
                                            {renderOrderBtn(order.status, order.id)}   
                                        </tr>
                                    </tbody>
                                    
                                </table>
                                
                            </div>
                            <div className="panel-body">
                                <Link to={`/detail/${order.asin}`} style={{textDecoration:"none" ,color:"#67AFCB"}}><h4>{order.productTitle}</h4></Link>
                                <p style={{display:"inline-block", margin:"20px"}}>Price: {order.currentPrice}</p>
                                <p style={{display:"inline-block", margin:"20px"}}>Quantity: {order.quantity}</p>
                                <p style={{display:"inline-block", margin:"20px"}}>Total Price: {order.totalPrice}</p>
                            </div>
                        </div>
                        <hr/>
                    </div>
                )
            })

        }
        else{
            return(
                <div className="emptyCart">
                    <b>Oops...! Your Orders List Is Empty</b>
                </div>
            )
        }

    }
    return(
        // <div className="container">
        //     <div className="ord-disp-container">
                
        //     </div>

        // </div>
        <div>
            {displayRender(props)}
        </div>
    );
}

export default MyOrders;